<template>
  <div class="process-page">
    <h2 class="title">Í≥µÏ†ï ÌùêÎ¶ÑÎèÑ Í¥ÄÎ¶¨</h2>

    <!-- Ï†úÌíà Í≤ÄÏÉâ -->
    <div class="product-search va-row va-gap-2 va-items-center">
      <label>Ï†úÌíàÏΩîÎìú: </label>
      <input v-model="search.product_code" placeholder="Ï†úÌíàÏΩîÎìú ÏûÖÎ†•" />

      <label>Ï†úÌíàÎ™Ö: </label>
      <input v-model="search.product_name" placeholder="Ï†úÌíàÎ™Ö ÏûÖÎ†•" />

      <label>Í∑úÍ≤©:</label>
      <input v-model="search.product_stand" placeholder="Í∑úÍ≤© ÏûÖÎ†•" />

      <button class="btn search" @click="handleProductSearch">Í≤ÄÏÉâ</button>
      <button class="btn reset" @click="resetSearch">Ï¥àÍ∏∞Ìôî</button>
      <button class="btn save" @click="saveProcesses">Ï†ÄÏû•</button>
    </div>

    <div class="times">
      <span class="product-label">Ï¥ù ÏÜåÏöîÏãúÍ∞Ñ: {{ totalProcessTime }}Î∂Ñ</span>
    </div>

    <!-- Í≥µÏ†ï Î™©Î°ù -->
    <div class="process-table">
      <div class="table-header">
        <h3>Í≥µÏ†ïÏàúÏÑú</h3>
        <div>
          <button class="btn add" @click="addProcess">Í≥µÏ†ïÏ∂îÍ∞Ä</button>
          <button class="btn delete" @click="deleteSelectedProcesses">Í≥µÏ†ïÏÇ≠Ï†ú</button>
        </div>
      </div>
      <table>
        <thead>
          <tr>
            <th><input type="checkbox" disabled /></th>
            <th>ÏàúÎ≤à</th>
            <th>ÏòàÏÉÅÏÜåÏöîÏãúÍ∞Ñ</th>
            <th>ÏãúÌóòÏûëÏóÖ</th>
            <th>ÏÑ§ÎπÑÏú†Ìòï</th>
            <th>ÏÉÅÏÑ∏Ï∂îÍ∞Ä</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(process, index) in processes" :key="index">
            <td><input type="checkbox" v-model="process.selected" /></td>
            <td>{{ index + 1 }}</td>
            <td><input class="time-input" v-model="process.process_time" placeholder="Ïòà: 60Î∂Ñ" /></td>
            <td>
              <select class="name-input" v-model="process.process_int">
                <option disabled value="">Í≥µÏ†ï Î™Ö</option>
                <option 
                  v-for="item in processIntList" 
                  :key="item.process_int" 
                  :value="item.process_int"
                >
                  {{ item.process_name }}
                </option>
              </select>
            </td>
            <td>
              <select v-if="equipmentCodes.length" class="equipment-select" v-model="process.code_value">
                <option disabled value="">ÏÑ†ÌÉù</option>
                <option v-for="item in equipmentCodes" :key="item.value" :value="item.value">
                  {{ item.label }}
                </option>
              </select>
              <span v-else style="color: red;">üö´ ÏÑ§ÎπÑ ÏΩîÎìú ÏóÜÏùå</span>
            </td>
            <td>
              <button class="btn save" @click="handlePopupOpen(process.process_code, index)">
                ÏÉÅÏÑ∏Ï∂îÍ∞Ä
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!-- ÌåùÏóÖ Ïª¥Ìè¨ÎÑåÌä∏ -->
    <PopupDetail
      v-if="popupVisible"
      :visible="popupVisible"
      :processCode="popupProcessCode"
      :productCode="popupProductCode"
      :materialOptions="materialOptions"
      :materialList="materialList"
      :bomCode="bomCode"
      @update:visible="popupVisible = $event"
      @save="saveMaterial"
      @materialCodeChange="onMaterialCodeChange"
      @addMaterial="addMaterial"
      @deleteSelectedMaterials="deleteSelectedMaterials"
    />
  </div>
</template>

<script lang="ts" setup>
import PopupDetail from '../modals/PopupDetail.vue'
import axios from 'axios'
import { onMounted ,computed, ref, watch } from 'vue'

interface Product {
  product_code: string
  product_name: string
  product_stand: string
}

interface Process {
  process_code: string
  process_time: string
  process_name: string
  code_value: string
  process_int: string
  selected?: boolean
}

interface ProcessPayload {
  process_code: string
  process_name: string
  process_time: string
  process_seq: number
  product_code: string
  code_value: string
  process_group_code: string
  process_int: string
}

interface EquipmentCode {
  value: string
  label: string
}

interface MaterialRow {
  process_code: string
  material_code: string
  material_name: string
  material_unit: string
  BOM_code: string
  usage_qty: number
  responsible: string
  selected?: boolean
}

interface MaterialOption {
  material_code: string
  material_name: string
  material_unit: string
  usage_qty: number
}

interface ProcessGroupPayload {
  process_group_code: string
  product_code: string
}

const selectedProductCode = ref<string>('')
const products = ref<Product[]>([])
const processes = ref<Process[]>([])
const equipmentCodes = ref<EquipmentCode[]>([])
const popupVisible = ref(false)
const popupProcessCode = ref('')
const materialList = ref<MaterialRow[]>([])
const materialOptions = ref<MaterialOption[]>([])
const popupProductCode = ref<string>('')
const bomCode = ref('')

const search = ref({
  product_code: '',
  product_name: '',
  product_stand: ''
})

const handleProductSearch = () => {
  let found: Product | null = null

  // Ïö∞ÏÑ† Ï†úÌíàÏΩîÎìúÎ°ú Ï∞æÍ∏∞
  if (search.value.product_code) {
    found = products.value.find(p => p.product_code === search.value.product_code) ?? null
  }

  // ÏïÑÎãàÎ©¥ Ï†úÌíàÎ™Ö + Í∑úÍ≤©ÏúºÎ°ú Ï∞æÍ∏∞
  if (!found && search.value.product_name && search.value.product_stand) {
    found = products.value.find(p =>
      p.product_name === search.value.product_name &&
      p.product_stand === search.value.product_stand
    ) ?? null
  }

  if (!found) {
    alert('Ìï¥Îãπ Ï†úÌíàÏùÑ Ï∞æÏùÑ Ïàò ÏóÜÏäµÎãàÎã§.')
    return
  }

  // ‚úÖ ÏûëÏÑ± Ï§ë Í≥µÏ†ïÏù¥ ÏûàÍ≥†, ÏÑ†ÌÉùÎêú Ï†úÌíàÍ≥º Îã§Î•∏ Í≤ΩÏö∞ ÌôïÏù∏ ÏïåÎ¶º
  const isEditing = processes.value.length > 0
  const isDifferentProduct = selectedProductCode.value && selectedProductCode.value !== found.product_code

  if (isEditing && isDifferentProduct) {
    const confirmed = confirm('ÌòÑÏû¨ Í≥µÏ†ï Ï†ïÎ≥¥Î•º ÏûëÏÑ± Ï§ëÏûÖÎãàÎã§.\nÏ†úÌíàÏùÑ Î≥ÄÍ≤ΩÌïòÏãúÍ≤†ÏäµÎãàÍπå?')
    if (!confirmed) {
      return
    }
  }

  // ‚úÖ Ï†úÌíà Î≥ÄÍ≤Ω Ïã§Ìñâ
  selectedProductCode.value = found.product_code
}

const resetSearch = () => {
  search.value = {
    product_code: '',
    product_name: '',
    product_stand: ''
  }
  selectedProductCode.value = ''
  processes.value = []
}

const processIntList = ref<{ process_int: string; process_name: string }[]>([])

const fetchProcessInt = async () => {
  try {
    const res = await axios.get('/processInit')  // Í≥µÏ†ïÎ™Ö Î¶¨Ïä§Ìä∏ API
    processIntList.value = res.data  // üëà Í∞ùÏ≤¥ Î∞∞Ïó¥ Ïú†ÏßÄ
  } catch (err) {
    console.error('‚ùå Í≥µÏ†ï Í∏∞Î≥∏ Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:', err)
  }
}

const fetchProducts = async () => {
  try {
    const res = await axios.get('/product')
    products.value = res.data
  } catch (err) {
    console.log('‚ùå Ï†úÌíà Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®:', err)
  }
}

const fetchEquipmentCodes = async () => {
  try{
    const res = await axios.get('/common-codes/?groups=0T')
    equipmentCodes.value = res.data['0T'] || []
  } catch(err) {
    console.error('‚ùå ÏÑ§ÎπÑÏú†Ìòï Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®:', err)
  }
}

const fetchMaterials = async () => {
  try {
    const res = await axios.get(`/bom/processList/${popupProductCode.value}`)
    
    materialOptions.value = res.data
    bomCode.value = res.data[0].bom_code
    console.log("ÏûêÏû¨:",res.data);
  } catch (err) {
    console.log('‚ùå ÏûêÏû¨ Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®:', err)
  }
}

const fetchProcess = async () => {
  try {
    const res = await axios.get(`/process/${selectedProductCode.value}`)
    const fetched = res.data

    // process_int Ïó≠Ï∂îÏ†Å
    processes.value = fetched.map((item: any) => {
      const match = processIntList.value.find(pi => pi.process_name === item.process_name)
      return {
        process_code: item.process_code,
        process_time: item.process_time,
        process_name: item.process_name,
        code_value: item.code_value,
        process_int: match?.process_int || '',  // ÏóÜÏúºÎ©¥ ÎπàÍ∞í Ï≤òÎ¶¨
        selected: false
      }
    })
  } catch (err) {
    console.log('‚ùå Í≥µÏ†ïÏ†ïÎ≥¥ Ï°∞Ìöå Ïã§Ìå®:', err)
  }
}

const fetchProcessDetail = async () => {
  try {
    const res = await axios.get(`/processDetail/${popupProcessCode.value}`)
    const fetchedDetails = res.data

    // material_code Í∏∞Ï§ÄÏúºÎ°ú name, unit Ï±ÑÏõå ÎÑ£Í∏∞
    materialList.value = fetchedDetails.map((item: any) => {
      const matched = materialOptions.value.find(opt => opt.material_code === item.material_code)

      return {
        process_code: item.process_code || popupProcessCode.value,
        material_code: item.material_code,
        BOM_code: item.BOM_code || '', // ÌïÑÏöî Ïãú
        usage_qty: item.usage_qty,
        responsible: item.name,
        material_name: matched?.material_name || '',
        material_unit: matched?.material_unit || '',
        selected: false
      }
    })
  } catch (err) {
    console.log('‚ùå ÏÉÅÏÑ∏Ï†ïÎ≥¥ Ï°∞Ìöå Ïã§Ìå®:', err)
  }
}

watch(popupProcessCode, (newCode) => {
  if (newCode) {
    fetchProcessDetail()
  }
})

watch(selectedProductCode, (newCode) => {
  if (newCode) {
    fetchProcess()
  }
})

const onMaterialCodeChange = (row: MaterialRow) => {
  const selected = materialOptions.value.find(m => m.material_code === row.material_code)
  if (selected) {
    row.material_name = selected.material_name
    row.material_unit = selected.material_unit
    row.usage_qty = selected.usage_qty
  }
}

const totalProcessTime = computed(() => {
  return processes.value.reduce((sum, p) => {
    const time = parseInt(p.process_time.replace(/[^\d]/g, ''))
    return sum + (isNaN(time) ? 0 : time)
  }, 0)
})

const addProcess = () => {
  processes.value.push({
    process_code:'',
    process_time: '',
    process_name: '',
    code_value: '',
    process_int: '', 
    selected: false
  })
}

const deleteSelectedProcesses = async () => {
  for (const p of processes.value) {
    if (p.selected && p.process_code) {
      try {
        await axios.delete(`/process/${p.process_code}`)
        console.log(`üóëÔ∏è ÏÑúÎ≤ÑÏóêÏÑú Í≥µÏ†ï ÏÇ≠Ï†ú ÏôÑÎ£å: ${p.process_code}`)
      } catch (err) {
        console.error(`‚ùå Í≥µÏ†ï ÏÇ≠Ï†ú Ïã§Ìå®: ${p.process_code}`, err)
        alert(`Í≥µÏ†ï ${p.process_code} ÏÇ≠Ï†ú Ïã§Ìå®!`)
      }
    }
  }
  // ‚úÖ ÏÑ†ÌÉùÎêú Ìï≠Î™©ÏùÄ Î™®Îëê Ï†úÍ±∞ (Îì±Î°ù Ï†Ñ/ÌõÑ ÏÉÅÍ¥ÄÏóÜÏù¥)
  processes.value = processes.value.filter(p => !p.selected)
}

const addMaterial = () => {
  materialList.value.push({
    process_code: '',
    material_code: '',
    material_name: '',
    material_unit: '',
    BOM_code: '',
    usage_qty: 0,
    responsible: '',
    selected: false
  })
}

const deleteSelectedMaterials = async () => {
  for (const row of materialList.value) {
    console.log("‚úÖ ÏÇ≠Ï†ú ÌõÑÎ≥¥:", row) // Ïù¥ Î°úÍ∑∏Î°ú Í∞í Ï†úÎåÄÎ°ú Îì§Ïñ¥Ïò§ÎäîÏßÄ ÌôïÏù∏
    if (row.selected && row.process_code && row.material_code) {
      try {
        await axios.delete(`/processDetail/${row.process_code}/${row.material_code}`)
      } catch (err) {
      }
    }
  }
  materialList.value = materialList.value.filter(row => !row.selected)
}

const saveMaterial = async (): Promise<void> => {
  // ÌòÑÏû¨ Í≥µÏ†ïÏóê Ìï¥ÎãπÌïòÎäî Î™®Îì† ÏûêÏû¨ Î®ºÏ†Ä ÏÇ≠Ï†ú
  try {
    await axios.delete(`/processDetail/${popupProcessCode.value}`)
    console.log(`‚úÖ ${popupProcessCode.value} Ïóê Ìï¥ÎãπÌïòÎäî Í∏∞Ï°¥ ÏûêÏû¨ ÏÇ≠Ï†ú ÏôÑÎ£å`)
  } catch (err) {
    console.error('‚ùå Í∏∞Ï°¥ ÏûêÏû¨ ÏÇ≠Ï†ú Ïã§Ìå®:', err)
    alert('Í∏∞Ï°¥ ÏûêÏû¨ ÏÇ≠Ï†ú Ï§ë Ïò§Î•ò Î∞úÏÉù!')
    return
  }

  // ÏÉàÎ°úÏö¥ ÏûêÏû¨ Ï†ÄÏû• (ÏÇ≠Ï†ú ÌõÑ insert)
  const payload = materialList.value.map(p => ({
    process_code: popupProcessCode.value,
    material_code: p.material_code,
    BOM_code: bomCode.value,
    name: p.responsible,
  }))

  console.log('üì¶ Ï†ÄÏû•Ìï† Ïû¨Î£å Îç∞Ïù¥ÌÑ∞:', payload)

  try {
    const res = await axios.post(`/process/${popupProcessCode.value}`, payload)
    if (res.data.isSuccessed === true) {
      alert('Î™®Îì† Ïû¨Î£å Îì±Î°ù ÏôÑÎ£å!')
      await fetchProcessDetail()
    } else {
      alert('Îì±Î°ù Ïã§Ìå®!')
    }
  } catch (err) {
    console.error('‚ùå ÏûêÏû¨ Îì±Î°ù Ïã§Ìå®:', err)
    alert('ÏÑúÎ≤Ñ Ïò§Î•ò Î∞úÏÉù!')
  }
}

const saveProcesses = async (): Promise<void> => {
  const insertList: ProcessPayload[] = [];
  const updateList: ProcessPayload[] = [];

  const group_code = `${selectedProductCode.value}-Process`;
  const groupItem: ProcessGroupPayload = {
    process_group_code: group_code,
    product_code: selectedProductCode.value
  };

  processes.value.forEach((p, idx) => {
    const code = `${selectedProductCode.value}Process${idx + 1}`;

    // üß† process_intÎ°úÎ∂ÄÌÑ∞ process_name ÎèôÍ∏∞Ìôî
    const matched = processIntList.value.find(item => item.process_int === p.process_int);
    const name = matched?.process_name || '';

    const payload: ProcessPayload = {
      process_code: code,
      process_name: name,
      process_time: p.process_time,
      process_seq: idx + 1,
      code_value: p.code_value,
      product_code: selectedProductCode.value,
      process_group_code: group_code,
      process_int: p.process_int
    };

    if (!p.process_code) {
      insertList.push(payload);
    } else {
      updateList.push(payload);
    }
  });

  try {
    const groupCheckRes = await axios.get(`/processG/${group_code}`);
    const groupExists = Array.isArray(groupCheckRes.data) && groupCheckRes.data.length > 0;

    if (insertList.length > 0) {
      if (!groupExists) {
        const groupRes = await axios.post('/processG', groupItem);
        if (!groupRes.data.isSuccessed) throw new Error('Í≥µÏ†ï Í∑∏Î£π Îì±Î°ù Ïã§Ìå®');
      }

      const processRes = await axios.post('/process', insertList);
      if (!processRes.data.isSuccessed) throw new Error('Í≥µÏ†ï Îì±Î°ù Ïã§Ìå®');
    }

    for (const payload of updateList) {
      try {
        const res = await axios.put(`/process/${payload.process_code}`, [payload]);
        if (!res.data.isSuccessed) {
          console.warn(`‚ö†Ô∏è ÏàòÏ†ï Ïã§Ìå®: ${payload.process_code}`);
        }
      } catch (err) {
        console.error(`‚ùå ÏàòÏ†ï Ï§ë Ïò§Î•ò: ${payload.process_code}`, err);
        alert(`Í≥µÏ†ï ÏàòÏ†ï Ï§ë Ïò§Î•ò Î∞úÏÉù: ${payload.process_code}`);
      }
    }

    alert('Í≥µÏ†ï Ï†ÄÏû• ÏôÑÎ£å!');
    await fetchProcess();
  } catch (err) {
    console.error('‚ùå Ï†ÄÏû• Ïã§Ìå®:', err);
    alert('Ï†ÄÏû• Ï§ë Ïò§Î•ò Î∞úÏÉù!');
  }
};


const openPopup = (processCode: string,  productCode: string): void => {
  popupProcessCode.value = processCode
  popupProductCode.value = productCode
  popupVisible.value = true
  fetchMaterials()
}

const handlePopupOpen = (processCode: string, index: number): void => {
  if (!processCode) {
    alert('Í≥µÏ†ïÏùÑ Î®ºÏ†Ä Ï†ÄÏû•Ìï¥Ïïº ÏÉÅÏÑ∏Ï†ïÎ≥¥Î•º Ï∂îÍ∞ÄÌï† Ïàò ÏûàÏäµÎãàÎã§.')
    return
  }

  const fullCode = `${selectedProductCode.value}Process${index + 1}`
  openPopup(fullCode, selectedProductCode.value)
}

onMounted(() => {
  fetchProducts()
  fetchEquipmentCodes()
  fetchMaterials()
  fetchProcess()
  fetchProcessInt()
})
</script>

<style scoped>
.process-page {
  padding: 30px;
  font-family: 'Pretendard', sans-serif;
  background: #fff;
}

.times {
  text-align: center;
}

h3 {
  font-size: 24px;
  font-weight: bold;
}

h2.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.product-select {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 20px;
}

.product-label {
  font-size: 14px;
  color: #333;
}

.btn {
  padding: 6px 12px;
  font-size: 14px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.btn.save {
  background-color: #2f80ed;
  color: white;
}

.btn.let {
  background-color: #535658;
  color: white;
}

.btn.delete {
  background-color: #eb5757;
  color: white;
}

.btn.add {
  background-color: #2f80ed;
  color: white;
  font-weight: 500;
}

.process-table .table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.process-table table {
  width: 100%;
  border-collapse: collapse;
}

.process-table th, .process-table td {
  border: 1px solid #e0e0e0;
  padding: 10px;
  text-align: center;
  font-size: 14px;
}

.time-input, .name-input, .equipment-select {
  width: 100%;
  padding: 6px;
  font-size: 14px;
  box-sizing: border-box;
}

.btn.search {
  background-color: #27ae60;
  color: white;
}

.btn.reset {
  background-color: #7f8c8d;
  color: white;
}
</style>